@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;700&display=swap);
body * {
  font-family: "IBM Plex Sans" !important;
  font-weight: 500;
}

main {
  padding: 0 !important;
}

.MuiDrawer-root {
  height: 100vh;
}

.menu-links {
  margin: 10px 0 !important;
  font-size: 14px !important;
  font-weight: 500 !important;
}

.menu-links:not(.static):hover,
.menu-links.Mui-selected {
  color: #ea2121 !important;
  background-color: rgb(234, 33, 33, 0.08) !important;
  border-radius: 4px;
}

.menu-group {
  margin-bottom: 30px;
}

.menu-links.static:hover,
.menu-links.static:active {
  background-color: transparent !important;
}

.MuiList-root {
  /* height: 100vh;
    padding: 10px !important; */
}

.menu-group.footer {
  position: fixed;
  bottom: 0;
  width: 278px;
}

.right-elem {
  flex: 1 1;
  text-align: right;
}

.left-elem {
  flex: 1 1;
  text-align: left;
}

.MuiDrawer-paperAnchorDockedLeft {
  border-right: 1px solid rgba(33, 37, 41, 0.16) !important;
}

.dark-mode div,
.dark-mode main {
  background-color: #181a1b;
  color: #ffffff;
  transition: background-color 0.25s ease-out;
}

.dark-mode .MuiDrawer-paperAnchorDockedLeft {
  border-right: 1px solid rgba(255, 255, 255, 0.16) !important;
}

.doc-wrapper {
  float: left;
}

.doc-wrapper .details {
  text-align: center;
}

.doc-wrapper .placeholder {
  padding: 20px;
  width: 256px;
  height: 255px;
  border: 3px dashed red;
  margin: 10px;
  background: url("https://www.associationservicesgroup.net/wp-content/uploads/2017/07/PDF-Placeholder-e1500896019213.png")
    no-repeat;
  background-size: 100%;
}

.mt-30 {
  margin-top: 30px !important;
}

.mt-10 {
  margin-top: 10px !important;
}

.mb-10 {
  margin-bottom: 10px !important;
}

.Layout-root-1 {
  min-height: 100vh;
}

.ereg-input {
  border: 1px solid rgba(33, 37, 41, 0.38);
  box-sizing: border-box;
  border-radius: 4px;
  width: 100%;
  padding: 4px 6px;
}

.ereg-input:focus {
  border: none !important;
}

.ereg-tag {
  font-size: 8px;
  font-style: normal;
  font-weight: 700;
  line-height: 10px;
  letter-spacing: 0em;
  text-align: left;
  color: rgb(234, 33, 33, 1);
  background-color: rgba(234, 33, 33, 0.08);
  padding: 5px;
  border-radius: 4px;
  margin-left: 10px;
}

.ereg-label {
  font-size: 12px;
  font-weight: 500;
  margin-bottom: 10px;
}

.silent {
  margin: 0;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: left;
}

.danger-text {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
  color: #ea2121;
}

.declaration-header {
  font-size: 14px;
  font-weight: bold;
  padding-top: 19px;
  padding-left: 25px;
  border-bottom: 1px solid rgba(33, 37, 41, 0.16);
  padding-bottom: 25px;
  padding-right: 10px;
}

input[type="number"] {
  text-align: right;
}

#category {
  padding: 13px !important;
}

.form-subtitle {
  font-weight: bolder !important;
  margin: 30px 0 14px 0 !important;
  /* text-decoration: underline; */
}

.subtitle-container {
  border-bottom: solid 1px #212529;
  margin-bottom: 16px;
}

.pull-right {
  float: right;
}

.pt-20 {
  padding-top: 20px !important;
}

.empty-tbl-text {
  text-align: center !important;
  color: rgba(33, 37, 41, 0.54) !important;
}

.action-anchor {
  font-weight: bold;
}

.ereg-cell {
  /* padding: 2px 6px !important; */
  padding: 2px !important;
}

.lodge-tbl-head {
  color: rgba(33, 37, 41, 0.54) !important;
  font-size: 12px !important;
  padding: 2px !important;
}

.dark-mode .lodge-tbl-head {
  color: #ffffff !important;
}

.first-col {
  text-align: center !important;
}

iframe {
  display: none;
}

.pre-sad {
  background-repeat: no-repeat;
  height: 100%;
  background-size: 100%;
  position: relative;
}

.erc-red {
  color: #ea2121 !important;
}

.erc-menu-item:hover {
  background-color: rgba(234, 33, 33, 0.08) !important;
}

.text-center {
  text-align: center !important;
}

.containers-qty {
  border: 1px solid rgba(33, 37, 41, 0.16);
  border-radius: 4px;
}

